import React, { useState, useEffect } from 'react';
import CalendarToolbar from './CalendarToolbar';
import NewCalendarView from './NewCalendarView';
import EventForm from './EventForm';
import EventDetailModal from './EventDetailModal';
import { getEvents, deleteEvent } from '../../googleCalendarService';
import { fetchSheetData } from '../../googleSheetsService';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import './calendar-theme.css'; // Custom calendar styles for Tailwind

import { Button } from '../ui/button';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter, DialogClose } from '../ui/dialog';
import { Alert, AlertDescription } from '../ui/alert';
import { ArrowLeft } from 'lucide-react';

const CALENDARS = {
  laPau: {
    id: 'c_classroom39c07066@group.calendar.google.com',
    name: 'Agenda del CFA La Pau'
  },
  docents: {
    id: 'c_5f59155c69967ab59b0214a1c9c0c44ae76cee4293190f7e948516ead7da7715@group.calendar.google.com',
    name: 'Calendari docents'
  },
  incidents: {
    id: 'incidents',
    name: 'Incidències'
  }
};

const transformIncidentsToEvents = (sheetData, profile) => {
  if (!sheetData || sheetData.length < 2) return [];
  const headers = sheetData[0];
  const data = sheetData.slice(1);

  const dateIniciIndex = headers.indexOf('Data Inici');
  const timeIniciIndex = headers.indexOf('Hora Inici');
  const dateFiIndex = headers.indexOf('Data Fi');
  const timeFiIndex = headers.indexOf('Hora Fi');
  const typeIndex = headers.indexOf('Tipus');
  const userIndex = headers.indexOf('Usuari (Email)');
  const deletedIndex = headers.indexOf('Esborrat');

  if (dateIniciIndex === -1 || typeIndex === -1 || userIndex === -1) return [];

  return data
    .filter(row => row[deletedIndex] !== 'TRUE' && row[dateIniciIndex])
    .map(row => {
      const parseDateTime = (dateStr, timeStr) => {
        let dateParts;
        if (dateStr.includes('/')) {
          dateParts = dateStr.split('/').map(p => parseInt(p));
          dateParts = [dateParts[2], dateParts[1] - 1, dateParts[0]];
        } else if (dateStr.includes('-')) {
          dateParts = dateStr.split('-').map(p => parseInt(p));
          dateParts = [dateParts[0], dateParts[1] - 1, dateParts[2]];
        } else return null;
        
        const timeParts = timeStr ? String(timeStr).split(':').map(p => parseInt(p)) : [0, 0];
        return new Date(dateParts[0], dateParts[1], dateParts[2], timeParts[0] || 0, timeParts[1] || 0);
      };

      const startDate = parseDateTime(row[dateIniciIndex], row[timeIniciIndex]);
      if (!startDate) return null;

      const isAllDay = !row[timeIniciIndex];
      let endDate = parseDateTime(row[dateFiIndex] || row[dateIniciIndex], row[timeFiIndex] || row[timeIniciIndex]);

      if (!endDate || endDate < startDate) {
          endDate = new Date(startDate);
          if (!isAllDay) endDate.setHours(startDate.getHours() + 1);
      }
      
      const userEmail = row[userIndex];
      let eventTitle = (profile.role === 'Gestor' || profile.role === 'Direcció') ? `${row[typeIndex]} (${userEmail})` : `Incidència de ${userEmail}`;
      let incidentType = (profile.email === userEmail) ? 'own' : 'other';
      
      return {
        title: eventTitle,
        start: startDate,
        end: endDate,
        allDay: isAllDay,
        resource: { incidentType, isIncident: true, rawData: row, headers },
      };
  }).filter(Boolean);
};

const formats = {
    timeGutterFormat: 'HH:mm',
    eventTimeRangeFormat: ({ start, end }, culture, local) =>
      local.format(start, 'HH:mm', culture) + ' - ' + local.format(end, 'HH:mm', culture),
    agendaTimeRangeFormat: ({ start, end }, culture, local) =>
      local.format(start, 'HH:mm', culture) + ' - ' + local.format(end, 'HH:mm', culture),
  };

function CalendarMainView({ onBackClick, accessToken, profile }) {
  const [activeCalendar, setActiveCalendar] = useState(CALENDARS.laPau.id);
  const [currentView, setCurrentView] = useState('month');
  const [calendarDate, setCalendarDate] = useState(new Date());
  const [events, setEvents] = useState([]);
  const [selectedEvent, setSelectedEvent] = useState(null);
  const [isFormOpen, setIsFormOpen] = useState(false);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);

  const fetchCalendarData = async () => {
    if (!accessToken || !profile) return;
    setLoading(true);
    setError(null);
    try {
      let fetchedEvents = [];
      if (activeCalendar === CALENDARS.incidents.id) {
        const incidentData = await fetchSheetData('Incidències!A:N', accessToken);
        fetchedEvents = transformIncidentsToEvents(incidentData, profile);
      } else {
        fetchedEvents = await getEvents(activeCalendar, accessToken);
      }
      setEvents(fetchedEvents);
    } catch (err) {
      console.error("Error fetching calendar data:", err);
      setError(err.message || 'Error en carregar les dades del calendari.');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchCalendarData();
  }, [activeCalendar, accessToken, profile]);

  const handleDeleteEvent = async (eventId) => {
    try {
      await deleteEvent(activeCalendar, eventId, accessToken);
      setSelectedEvent(null);
      fetchCalendarData();
    } catch (err) {
      setError(err.message || 'Error en esborrar l\'esdeveniment.');
    }
  };

  const handleSelectSlot = (slotInfo) => {
    // Do nothing for now
  };

  const eventPropGetter = (event) => {
    const isIncident = event.resource?.isIncident;
    const incidentType = event.resource?.incidentType;
    let className = 'rbc-event-custom';
    if (isIncident) {
      className += incidentType === 'own' ? ' rbc-event-incident-own' : ' rbc-event-incident-other';
    } else {
      const category = (event.title || '').split(':')[0].trim();
      className += ` rbc-event-category-${category.toLowerCase().replace(/\s+/g, '-')}`;
    }
    return { className };
  };

  const activeCalendarName = Object.values(CALENDARS).find(c => c.id === activeCalendar)?.name || '';

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <header className="flex justify-between items-center mb-6 pb-4 border-b">
        <div className="flex items-center gap-4">
          <Button onClick={onBackClick} variant="outline" size="icon"><ArrowLeft className="h-4 w-4" /></Button>
          <h1 className="text-2xl font-bold">Calendaris del centre</h1>
        </div>
        <div className="text-right">
          <div className="font-semibold">{profile.name} ({profile.role})</div>
          <div className="text-xs text-muted-foreground">{profile.email}</div>
        </div>
      </header>

      <CalendarToolbar 
        calendars={CALENDARS}
        activeCalendar={activeCalendar}
        setActiveCalendar={setActiveCalendar}
        currentView={currentView}
        setCurrentView={setCurrentView}
        profile={profile}
        onAddEventClick={() => setIsFormOpen(true)}
        date={calendarDate}
        onNavigate={setCalendarDate}
      />

      {error && <Alert variant="destructive" className="mt-4"><AlertDescription>{error}</AlertDescription></Alert>}

      <EventForm 
        isOpen={isFormOpen}
        onClose={() => setIsFormOpen(false)}
        accessToken={accessToken}
        calendarId={activeCalendar}
        calendarName={activeCalendarName}
        onEventCreated={fetchCalendarData}
        eventToEdit={selectedEvent?.resource?.isIncident ? null : selectedEvent}
      />

      <EventDetailModal 
        event={selectedEvent}
        onClose={() => setSelectedEvent(null)}
        calendarName={activeCalendarName}
        profile={profile}
        onDelete={handleDeleteEvent}
        onEdit={() => {
          setIsFormOpen(true);
        }}
      />

      {loading ? (
        <p className="text-center text-muted-foreground mt-4">Carregant esdeveniments...</p>
      ) : (
        <div className="mt-4 bg-white p-4 rounded-lg shadow">
          <NewCalendarView events={events} currentCalendarId={activeCalendar} profile={profile} />
          
          
        </div>
      )}
    </div>
  );
}

export default CalendarMainView;