import React, { useState } from 'react';
import { addAviso } from '../avisosService';
import { Button } from './ui/button';
import { Input } from './ui/input';
import { Label } from './ui/label';
import { Textarea } from './ui/textarea';
import { Alert, AlertDescription } from './ui/alert';

const AvisoForm = ({ accessToken, onAvisoAdded, onCancel }) => {
  const [titol, setTitol] = useState('');
  const [contingut, setContingut] = useState('');
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!titol || !contingut) {
      setError('El títol i el contingut no poden estar buits.');
      return;
    }

    setLoading(true);
    setError(null);

    try {
      const payload = { Titol: titol, Contingut: contingut };
      await addAviso(payload, accessToken);
      alert('Avís afegit correctament!');
      onAvisoAdded();
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      {error && <Alert variant="destructive"><AlertDescription>{error}</AlertDescription></Alert>}
      <div className="grid gap-2">
        <Label htmlFor="titol">Títol</Label>
        <Input 
          id="titol" 
          value={titol}
          onChange={(e) => setTitol(e.target.value)}
          required
        />
      </div>
      <div className="grid gap-2">
        <Label htmlFor="contingut">Contingut</Label>
        <Textarea 
          id="contingut" 
          rows="10"
          value={contingut}
          onChange={(e) => setContingut(e.target.value)}
          required
        />
        <p className="text-sm text-muted-foreground">
          Pots fer servir etiquetes HTML bàsiques com ara &lt;b&gt;, &lt;i&gt;, i &lt;a href="..."&gt;.
        </p>
      </div>
      <div className="flex justify-end gap-2 mt-4">
        <Button type="button" variant="secondary" onClick={onCancel}>Cancel·lar</Button>
        <Button type="submit" variant="default" disabled={loading}>
          {loading ? 'Guardant...' : 'Guardar Avís'}
        </Button>
      </div>
    </form>
  );
};

export default AvisoForm;
