import React, { useState, useEffect } from 'react';
import { getActiveAvisos } from '../avisosService';
import { Button } from './ui/button';
import { Card, CardHeader, CardTitle, CardContent, CardFooter, CardDescription } from './ui/card';
import { Users, Calendar, Group, Computer, Wrench, Bell, LogOut } from 'lucide-react';

const HomeView = ({ onIncidentsClick, onCalendarClick, onGroupsClick, onTICIncidentsClick, onMantenimentClick, onAvisosClick, profile, onLogout, accessToken }) => {
  const [avisos, setAvisos] = useState([]);
  const [loadingAvisos, setLoadingAvisos] = useState(true);

  useEffect(() => {
    const fetchAvisos = async () => {
      if (!accessToken) return;
      try {
        setLoadingAvisos(true);
        const activeAvisos = await getActiveAvisos(accessToken);
        setAvisos(activeAvisos);
      } catch (error) {
        console.error("Error fetching announcements:", error);
      } finally {
        setLoadingAvisos(false);
      }
    };

    fetchAvisos();
  }, [accessToken]);

  const menuItems = [
    { name: 'Incidències de personal', action: onIncidentsClick, icon: <Users className="w-8 h-8 mb-2" />, roles: ['all'] },
    { name: 'Calendaris del centre', action: onCalendarClick, icon: <Calendar className="w-8 h-8 mb-2" />, roles: ['all'] },
    { name: 'Grups d\'alumnes', action: onGroupsClick, icon: <Group className="w-8 h-8 mb-2" />, roles: ['all'] },
    { name: 'Incidències TIC', action: onTICIncidentsClick, icon: <Computer className="w-8 h-8 mb-2" />, roles: ['all'] },
    { name: 'Incidències Manteniment', action: onMantenimentClick, icon: <Wrench className="w-8 h-8 mb-2" />, roles: ['Gestor', 'Direcció'] },
  ];

  return (
    <div className="min-h-screen bg-gray-50 p-4 sm:p-6 lg:p-8">
      {/* Top Bar with Profile and Logout */}
      <header className="flex justify-between items-center mb-6 pb-4 border-b">
        <div className="text-left">
          <h1 className="text-2xl font-bold">App Gestió CFA LA PAU</h1>
          <p className="text-sm text-muted-foreground">Benvingut/da, {profile.name}</p>
        </div>
        <div className="flex items-center gap-4">
            <div className="text-right">
                <div className="font-semibold">{profile.role}</div>
                <div className="text-xs text-muted-foreground">{profile.email}</div>
            </div>
            <Button onClick={onLogout} variant="outline" size="icon">
                <LogOut className="h-4 w-4" />
            </Button>
        </div>
      </header>

      <main className="grid lg:grid-cols-3 gap-6">
        {/* Left Column: Main Menu */}
        <div className="lg:col-span-2">
            <div className="grid md:grid-cols-2 gap-6">
                {menuItems.map(item => {
                    if (item.roles.includes('all') || item.roles.includes(profile.role)) {
                        return (
                            <Card key={item.name} onClick={item.action} className="cursor-pointer hover:shadow-lg transition-shadow flex flex-col items-center justify-center text-center p-6">
                                {item.icon}
                                <h3 className="text-lg font-semibold">{item.name}</h3>
                            </Card>
                        )
                    }
                    return null;
                })}
            </div>
        </div>

        {/* Right Column: Announcements */}
        <div className="lg:col-span-1">
            <Card className="h-full flex flex-col">
                <CardHeader>
                    <CardTitle className="flex items-center gap-2"><Bell />Avisos</CardTitle>
                </CardHeader>
                <CardContent className="flex-grow">
                {loadingAvisos ? (
                    <p className="text-muted-foreground">Carregant avisos...</p>
                ) : (
                    <ul className="space-y-4">
                        <li className="border-b pb-2">
                            <a href="https://sites.google.com/cfalapau.cat/documents-docents" target="_blank" rel="noopener noreferrer" className="hover:underline text-blue-600">
                            Documents per als docents
                            </a>
                        </li>
                        {avisos.map(aviso => (
                            <li key={aviso.ID} className="border-b pb-2">
                                <h6 className="font-semibold">{aviso.Titol}</h6>
                                <div className="text-sm text-muted-foreground" dangerouslySetInnerHTML={{ __html: aviso.Contingut }} />
                            </li>
                        ))}
                    </ul>
                )}
                </CardContent>
                <CardFooter className="mt-auto">
                    <Button onClick={onAvisosClick} className="w-full">Tots els Avisos</Button>
                </CardFooter>
            </Card>
        </div>
      </main>
    </div>
  );
};

export default HomeView;
