import React, { useState, useEffect } from 'react';
import { GoogleOAuthProvider, useGoogleLogin } from '@react-oauth/google';
import { 
  fetchSheetData, 
  appendSheetData, 
  updateSheetData, 
  getUserProfile, 
  getUsers, 
  getIncidentTypes 
} from './googleSheetsService';
import AddIncidentForm from './components/AddIncidentForm';
import AnnualSummaryView from './components/AnnualSummaryView';
import DocumentationView from './components/DocumentationView';
import LoginView from './components/LoginView';
import HomeView from './components/HomeView';
import MyGroupsView from './components/MyGroupsView';
import CalendarMainView from './components/calendar/CalendarMainView';
import TICIncidentsView from './components/TICIncidentsView';
import MantenimentView from './components/MantenimentView';
import AvisosView from './components/AvisosView';

// New UI components
import { Button } from './components/ui/button';
import { Card, CardHeader, CardTitle, CardContent, CardFooter, CardDescription } from './components/ui/card';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter, DialogClose } from './components/ui/dialog';
import { Input } from './components/ui/input';
import { Label } from './components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from './components/ui/select';

// Added imports for components used in IncidentsView
import { ArrowLeft, Plus, Search, Info, Edit, Download, CheckCircle, XCircle } from 'lucide-react';
import { Alert, AlertDescription } from './components/ui/alert';
import { Table, TableHeader, TableBody, TableRow, TableHead, TableCell } from './components/ui/table';
import { Tooltip, TooltipProvider, TooltipTrigger, TooltipContent } from './components/ui/tooltip';
// import { Tabs, TabsList, TabsTrigger, TabsContent } from './components/ui/tabs'; // Commented out as tabs.jsx was not found

const App = () => {
  // Helper function for date formatting
  const formatDateForDisplay = (dateString) => {
    if (!dateString) return '';
    const date = new Date(dateString);
    if (isNaN(date.getTime())) {
      // Handle cases where dateString might be in 'DD/MM/YYYY' format
      const parts = dateString.split('/');
      if (parts.length === 3) {
        const d = parseInt(parts[0]);
        const m = parseInt(parts[1]) - 1; // Month is 0-indexed
        const y = parseInt(parts[2]);
        const newDate = new Date(y, m, d);
        if (!isNaN(newDate.getTime())) {
          return newDate.toLocaleDateString('ca-ES');
        }
      }
      return dateString; // Return original string if parsing fails
    }
    return date.toLocaleDateString('ca-ES');
  };

  const [currentScreen, setCurrentScreen] = useState('login'); // login, home, incidents, groups
  const [profile, setProfile] = useState(null);
  const [error, setError] = useState(null);
  const [masterIncidents, setMasterIncidents] = useState([]); // Holds all incidents from the sheet
  const [incidents, setIncidents] = useState([]); // Holds filtered incidents for display
  const [modifiedIncidents, setModifiedIncidents] = useState([]); // Holds incidents modified after signing
  const [users, setUsers] = useState([]);
  const [incidentTypes, setIncidentTypes] = useState([]);
  const [filterUser, setFilterUser] = useState('');
  const [filterYear, setFilterYear] = useState('');
  const [editingIncident, setEditingIncident] = useState(null);
  const [isSignaturePopupOpen, setIsSignaturePopupOpen] = useState(false);
  const [incidentToSign, setIncidentToSign] = useState(null);
  const [signatureType, setSignatureType] = useState('');
  const [accessToken, setAccessToken] = useState(() => localStorage.getItem('googleAccessToken'));

  // Effect to load profile if accessToken is already present (from localStorage)
  useEffect(() => {
    const loadProfileFromToken = async () => {
      if (accessToken) {
        try {
          const googleProfile = await fetch('https://www.googleapis.com/oauth2/v3/userinfo', {
            headers: { 'Authorization': `Bearer ${accessToken}` },
          }).then(res => res.json());

          const userProfile = await getUserProfile(googleProfile.email, accessToken);

          if (userProfile) {
            setProfile(userProfile);
            setCurrentScreen('home');
          } else {
            setError("Accés no autoritzat. El vostre correu electrònic no es troba a la llista d'usuaris permesos.");
            setProfile(null);
            setAccessToken(null);
            localStorage.removeItem('googleAccessToken');
          }
        } catch (err) {
          console.error(err);
          setError(err.message || "Ha ocorregut un error durant la càrrega del perfil.");
          setProfile(null);
          setAccessToken(null);
          localStorage.removeItem('googleAccessToken');
        }
      }
    };
    loadProfileFromToken();
  }, []); // Run only once on mount

  const handleSignClick = (incidentData, originalSheetRowIndex, type) => {
    if (!isValidSignDate(incidentData)) {
      setError("No es pot signar una incidència abans de la seva data de finalització (o d'inici si no té data de fi).");
      return;
    }

    setIncidentToSign({ data: incidentData, originalSheetRowIndex: originalSheetRowIndex });
    setSignatureType(type);
    setIsSignaturePopupOpen(true);
  };

  const handleConfirmSignature = async () => {
    if (!incidentToSign || !profile) return;

    const { data, originalSheetRowIndex } = incidentToSign;
    const updatedIncidentData = [...data];

    const now = new Date().toLocaleString('es-ES', {
      year: 'numeric', month: '2-digit', day: '2-digit',
      hour: '2-digit', minute: '2-digit', second: '2-digit',
      hour12: false
    });

    if (signatureType === 'user') {
      updatedIncidentData[8] = 'TRUE';
      updatedIncidentData[9] = now;
    }

    if (signatureType === 'director') {
      updatedIncidentData[10] = 'TRUE';
      updatedIncidentData[11] = now;
    }

    try {
      await updateSheetData(`Incidències!A${originalSheetRowIndex}:N${originalSheetRowIndex}`, [updatedIncidentData], accessToken);
      fetchIncidents(); // Refetch all data after a change
      setIsSignaturePopupOpen(false);
      setIncidentToSign(null);
      setSignatureType('');
    } catch (err) {
      console.error("Error signing incident:", err);
      setError("Error al signar la incidència. (Detalls: " + err.message + ")");
      setIsSignaturePopupOpen(false);
    }
  };

  const isValidSignDate = (incidentData) => {
    const headers = masterIncidents.length > 0 ? masterIncidents[0] : [];
    const dataIniciIndex = headers.indexOf('Data Inici');
    const dataFiIndex = headers.indexOf('Data Fi');

    const dataIniciStr = incidentData[dataIniciIndex] ? incidentData[dataIniciIndex].trim() : '';
    const dataFiStr = incidentData[dataFiIndex] ? incidentData[dataFiIndex].trim() : '';

    const targetDateStr = dataFiStr || dataIniciStr;

    if (!targetDateStr) {
      return false; // No date to check against
    }

    let targetDate;
    if (targetDateStr.includes('/')) {
      const parts = targetDateStr.split('/');
      targetDate = new Date(parts[2], parts[1] - 1, parts[0]);
    } else if (targetDateStr.includes('-')) {
      const parts = targetDateStr.split('-');
      targetDate = new Date(parts[0], parts[1] - 1, parts[2]);
    } else {
      return false; // Invalid date format
    }

    const today = new Date();
    targetDate.setHours(0, 0, 0, 0);
    today.setHours(0, 0, 0, 0);

    return today >= targetDate; // True if today is on or after targetDate
  };

  const handleCancelSignature = () => {
    setIsSignaturePopupOpen(false);
    setIncidentToSign(null);
    setSignatureType('');
  };

  const handleEditClick = (incidentData, originalSheetRowIndex) => {
    setEditingIncident({ data: incidentData, originalSheetRowIndex: originalSheetRowIndex });
  };

  const handleCloseForm = () => {
    setEditingIncident(null);
  };

  const handleSaveIncident = async (incidentData, originalSheetRowIndex) => {
    try {
      const isEdit = originalSheetRowIndex !== null;

      if (!isEdit) {
        // It's a new incident, just append it
        await appendSheetData('Incidències!A:N', [incidentData], accessToken);
      } else {
        const originalIncident = editingIncident.data;
        const isUserSigned = originalIncident[8] === 'TRUE';
        const isDirectorSigned = originalIncident[10] === 'TRUE';

        if (isUserSigned || isDirectorSigned) {
          // Signed incident: mark old as deleted and create a new one
          
          // 1. Mark original as deleted
          const deletedIncidentData = [...originalIncident];
          deletedIncidentData[12] = 'TRUE'; // Set 'Esborrat' to TRUE
          await updateSheetData(`Incidències!A${originalSheetRowIndex}:N${originalSheetRowIndex}`, [deletedIncidentData], accessToken);

          // 2. Create new incident with the changes
          // The incidentData from the form already has signatures cleared
          await appendSheetData('Incidències!A:N', [incidentData], accessToken);

        } else {
          // Not signed, just update it
          await updateSheetData(`Incidències!A${originalSheetRowIndex}:N${originalSheetRowIndex}`, [incidentData], accessToken);
        }
      }

      fetchIncidents(); // Refetch all data after a change
      setEditingIncident(null);
    } catch (err) {
      console.error("Error saving incident:", err);
      setError("Error en guardar la incidència. Verifiqueu la configuració y els permisos. (Detalles: " + err.message + ")");
    }
  };

  // Initial data fetch
  const fetchIncidents = async () => {
    if (!accessToken) return;
    try {
      const data = await fetchSheetData('Incidències!A:N', accessToken);
      setMasterIncidents(data);
    } catch (err) {
      console.error("Error fetching incidents:", err);
      setError("Error en carregar les incidències. Verifiqueu la configuració y els permisos.");
    }
  };

  useEffect(() => {
    if (currentScreen === 'incidents' || currentScreen === 'tic-incidents' || currentScreen === 'manteniment-incidents') {
      fetchIncidents();
      
      if (accessToken) {
        const loadFormDependencies = async () => {
          try {
            const [usersData, typesData] = await Promise.all([
              getUsers(accessToken),
              getIncidentTypes(accessToken)
            ]);
            setUsers(usersData); 
            setIncidentTypes(typesData);
          } catch (err) {
            console.error("Error loading form dependencies:", err);
            setError("Error en carregar les dades per als formularis (usuaris/tipus).");
          }
        };
        loadFormDependencies();
      }
    }
  }, [accessToken, currentScreen]);

  // Filtering logic
  useEffect(() => {
    if (masterIncidents.length === 0) {
        setIncidents([]);
        setModifiedIncidents([]);
        return;
    }

    const headers = masterIncidents[0];
    const dataIniciIndex = headers.indexOf('Data Inici');
    const userEmailIndex = headers.indexOf('Usuari (Email)');
    const exerciseIndex = headers.indexOf('Exercici');
    const deletedIndex = headers.indexOf('Esborrat');

    // 1. Map and pre-filter rows that are empty
    let allRows = masterIncidents.slice(1).map((row, index) => ({
        data: row,
        originalSheetRowIndex: index + 2
    })).filter(item => item.data[dataIniciIndex] && item.data[dataIniciIndex].trim() !== '');

    // Separate deleted and active incidents
    const activeRows = allRows.filter(item => item.data[deletedIndex] !== 'TRUE');
    const deletedRows = allRows.filter(item => item.data[deletedIndex] === 'TRUE');

    let filteredActiveRows = activeRows;
    let filteredDeletedRows = deletedRows;

    // 2. Apply user filter
    if (filterUser) {
        filteredActiveRows = filteredActiveRows.filter(item =>
            item.data[userEmailIndex] && item.data[userEmailIndex].toLowerCase().includes(filterUser.toLowerCase())
        );
        filteredDeletedRows = filteredDeletedRows.filter(item =>
            item.data[userEmailIndex] && item.data[userEmailIndex].toLowerCase().includes(filterUser.toLowerCase())
        );
    }

    // 3. Apply year filter
    if (filterYear) {
        filteredActiveRows = filteredActiveRows.filter(item => {
            const year = item.data[exerciseIndex];
            return year && year.toString() === filterYear;
        });
        filteredDeletedRows = filteredDeletedRows.filter(item => {
            const year = item.data[exerciseIndex];
            return year && year.toString() === filterYear;
        });
    }

    // 4. Set the final filtered incidents for display
    setIncidents([headers, ...filteredActiveRows]);
    setModifiedIncidents([headers, ...filteredDeletedRows]);

  }, [masterIncidents, filterUser, filterYear]);

  // Set the user filter automatically when the profile is loaded
  useEffect(() => {
    if (profile && profile.role === 'Usuari') {
      setFilterUser(profile.email);
    }
  }, [profile]);

  const login = useGoogleLogin({
    onSuccess: async (tokenResponse) => {
      setAccessToken(tokenResponse.access_token);
      localStorage.setItem('googleAccessToken', tokenResponse.access_token);
      try {
        const googleProfile = await fetch('https://www.googleapis.com/oauth2/v3/userinfo', {
          headers: { 'Authorization': `Bearer ${tokenResponse.access_token}` },
        }).then(res => res.json());

        const userProfile = await getUserProfile(googleProfile.email, tokenResponse.access_token);

        if (userProfile) {
          setProfile(userProfile);
          setCurrentScreen('home');
        } else {
          setError("Accés no autoritzat. El vostre correu electrònic no es troba a la llista d'usuaris permesos.");
          setProfile(null);
          setAccessToken(null);
          localStorage.removeItem('googleAccessToken');
        }
      } catch (err) {
        console.error(err);
        setError(err.message || "Ha ocorregut un error durant l'inici de sessió.");
        setProfile(null);
        setAccessToken(null);
        localStorage.removeItem('googleAccessToken');
      }
    },
    onError: () => {
      setError("Error d'inici de sessió. Si us plau, torneu a intentar-ho.");
    },
    scope: 'https://www.googleapis.com/auth/spreadsheets https://www.googleapis.com/auth/classroom.courses.readonly https://www.googleapis.com/auth/chat.spaces.readonly https://www.googleapis.com/auth/contacts.readonly https://www.googleapis.com/auth/classroom.rosters.readonly https://www.googleapis.com/auth/chat.memberships https://www.googleapis.com/auth/admin.directory.group.readonly https://www.googleapis.com/auth/calendar.events https://www.googleapis.com/auth/calendar.readonly',
  });

  const handleLogout = () => {
    setProfile(null);
    setAccessToken(null);
    setUsers([]);
    setIncidentTypes([]);
    setMasterIncidents([]);
    localStorage.removeItem('googleAccessToken');
    setCurrentScreen('login');
  };

  const IncidentsView = ({ onBackClick }) => {
    const exerciseIndex = masterIncidents.length > 0 ? masterIncidents[0].indexOf('Exercici') : -1;
    const availableYears = exerciseIndex === -1 ? [] : [...new Set(masterIncidents.slice(1).map(item => item[exerciseIndex]))].filter(Boolean).sort((a, b) => b - a);

    const headers = incidents.length > 0 ? incidents[0] : [];
    const dataIniciIndex = headers.indexOf('Data Inici');
    const horaIniciIndex = headers.indexOf('Hora Inici');
    const dataFiIndex = headers.indexOf('Data Fi');
    const horaFiIndex = headers.indexOf('Hora Fi');
    const signaturaUsuariIndex = headers.indexOf('Signatura Usuari');
    const timestampSignaturaUsuariIndex = headers.indexOf('Timestamp Signatura Usuari');
    const signaturaDireccioIndex = headers.indexOf('Signatura Direcció');
    const timestampSignaturaDireccioIndex = headers.indexOf('Timestamp Signatura Direcció');
    const userEmailIndex = headers.indexOf('Usuari (Email)');
    const duracioIndex = headers.indexOf('Duració');
    const tipusIndex = headers.indexOf('Tipus');
    const esborratIndex = headers.indexOf('Esborrat');
    const observacionsIndex = headers.indexOf('Observacions');

    const getStatusColor = (isUserSigned, isDirectorSigned) => {
      if (isDirectorSigned) return 'bg-green-100 border-green-200 text-green-800';
      if (isUserSigned) return 'bg-yellow-100 border-yellow-200 text-yellow-800';
      return 'bg-blue-100 border-blue-200 text-blue-800';
    };

    return (
      <div className="p-4 sm:p-6 lg:p-8">
        <header className="flex justify-between items-center mb-6 pb-4 border-b">
          <div className="flex items-center gap-4">
            <Button onClick={onBackClick} variant="outline" size="icon">
              <ArrowLeft className="h-4 w-4" />
            </Button>
            <h1 className="text-2xl font-bold">Incidències de personal</h1>
          </div>
          {profile && (
            <div className="text-right">
              <div className="font-semibold">{profile.name} ({profile.role})</div>
              <div className="text-xs text-muted-foreground">{profile.email}</div>
            </div>
          )}
        </header>

        {error && <Alert variant="destructive" className="mb-4"><button onClick={() => setError(null)} className="float-right font-bold">X</button><AlertDescription>{error}</AlertDescription></Alert>}

        {/* Tabs component is missing, so commenting out for now */}
        {/*
        <Tabs defaultValue="list" className="w-full">
          <TabsList className="grid w-full grid-cols-4">
            <TabsTrigger value="list">Llista</TabsTrigger>
            <TabsTrigger value="modified">Modificades</TabsTrigger>
            <TabsTrigger value="summary">Resum Anual</TabsTrigger>
            <TabsTrigger value="documentation">Documentació</TabsTrigger>
          </TabsList>

          <TabsContent value="list">
        */}
            <Card className="mt-4">
              <CardHeader>
                <CardTitle>Filtres i Accions</CardTitle>
              </CardHeader>
              <CardContent className="grid sm:grid-cols-2 md:grid-cols-3 gap-4 items-end">
                <div>
                  <Label htmlFor="filterUser">Filtrar per Usuari</Label>
                  { (profile?.role === 'Gestor' || profile?.role === 'Direcció') ? (
                    <Select value={filterUser} onValueChange={setFilterUser}>
                      <SelectTrigger><SelectValue placeholder="Tots els usuaris" /></SelectTrigger>
                      <SelectContent>
                        <SelectItem value="all">Tots els usuaris</SelectItem>
                        {users.sort((a, b) => a.name.localeCompare(b.name)).map(user => <SelectItem key={user.email} value={user.email}>{user.name}</SelectItem>)}
                      </SelectContent>
                    </Select>
                  ) : (
                    <Input type="text" id="filterUser" value={profile.email} readOnly />
                  )}
                </div>
                <div>
                  <Label htmlFor="filterYear">Filtrar per Any</Label>
                  <Select value={filterYear} onValueChange={setFilterYear}>
                    <SelectTrigger><SelectValue placeholder="Tots els anys" /></SelectTrigger>
                    <SelectContent>
                      <SelectItem value="all">Tots els anys</SelectItem>
                      {availableYears.map(year => <SelectItem key={year} value={year}>{year}</SelectItem>)}
                    </SelectContent>
                  </Select>
                </div>
                <Button onClick={() => setEditingIncident({ data: null, originalSheetRowIndex: null })}><Plus className="mr-2 h-4 w-4"/>Nova Incidència</Button>
              </CardContent>
            </Card>

            {editingIncident !== null && (
              <Card className="mt-6 p-4"> {/* Using Card for consistent styling */}
                <CardHeader>
                  <CardTitle>{editingIncident.data ? 'Editar Incidència' : 'Afegir Nova Incidència'}</CardTitle>
                  <CardDescription>Formulari per editar o afegir una nova incidència.</CardDescription>
                </CardHeader>
                <CardContent>
                  <AddIncidentForm
                    incidentToEdit={editingIncident.data}
                    originalSheetRowIndex={editingIncident.originalSheetRowIndex}
                    onSaveIncident={handleSaveIncident}
                    onClose={handleCloseForm} // This will now just hide the form
                    setError={setError}
                    profile={profile}
                    users={users}
                    incidentTypes={incidentTypes}
                  />
                </CardContent>
              </Card>
            )}

            <div className="rounded-md border overflow-hidden shadow-sm mt-6">
<Table>
          <TableHeader className="bg-primary">
            <TableRow className="hover:bg-primary/90">
      <TableHead className="w-[120px] text-white">Tipus</TableHead>
      <TableHead className="w-[100px] text-white">Usuari</TableHead>
      <TableHead className="w-[90px]  whitespace-pre-line text-white">Inici</TableHead>
      <TableHead className="w-[90px]  whitespace-pre-line text-white">Fi</TableHead>
      <TableHead className="w-[70px] text-white">Duració</TableHead>
      <TableHead className="w-[90px] text-white">Signat Usuari</TableHead>
      <TableHead className="w-[90px] text-white">Signat Direcció</TableHead>
      {/* Tres columnas de acciones sin encabezado */}
      <TableHead className="w-[30px]" />
      <TableHead className="w-[30px]" />
      <TableHead className="w-[100px]" />
    </TableRow>
  </TableHeader>

  <TableBody>
    {incidents.length === 1 ? (
      <TableRow>
        <TableCell colSpan={10} className="h-24 text-center text-muted-foreground">
          No hi ha incidències per mostrar.
        </TableCell>
      </TableRow>
    ) : (
      incidents.slice(1).map((item) => {
        const isUserSigned = item.data[signaturaUsuariIndex] === "TRUE"
        const isDirectorSigned = item.data[signaturaDireccioIndex] === "TRUE"
        const canSign = isValidSignDate(item.data)
        const canUserSign =
          profile?.role === "Usuari" && !isUserSigned && !isDirectorSigned && canSign
        const canDirectorSign =
          profile?.role === "Direcció" && !isDirectorSigned && canSign
        const canEdit =
          !isDirectorSigned || (isDirectorSigned && profile.role === "Direcció")
        const observacions = item.data[observacionsIndex]

        return (
          <TableRow
            key={item.originalSheetRowIndex}
            className="hover:bg-muted/50 data-[state=selected]:bg-muted"
          >
            <TableCell className="w-[120px]">{item.data[tipusIndex]}</TableCell>
            <TableCell className="w-[100px]">{item.data[userEmailIndex]}</TableCell>
            <TableCell className="w-[90px] whitespace-pre-line">
              {formatDateForDisplay(item.data[dataIniciIndex])}
              <br />
              {item.data[horaIniciIndex]}
            </TableCell>
            <TableCell className="w-[90px] text-xs whitespace-pre-line">
              {formatDateForDisplay(item.data[dataFiIndex])}
              <br />
              {item.data[horaFiIndex]}
            </TableCell>
            <TableCell className="w-[70px]">{item.data[duracioIndex]}</TableCell>
            <TableCell className="w-[90px]">
              {isUserSigned ? (
                <CheckCircle className="h-4 w-4 text-green-600" />
              ) : (
                <XCircle className="h-4 w-4 text-red-600" />
              )}
            </TableCell>
            <TableCell className="w-[90px]">
              {isDirectorSigned ? (
                <CheckCircle className="h-4 w-4 text-green-600" />
              ) : (
                <XCircle className="h-4 w-4 text-red-600" />
              )}
            </TableCell>

            {/* Columna 1: Info */}
            <TableCell className="w-[30px] text-center">
              {observacions && (
                <Tooltip>
                  <TooltipTrigger asChild>
                    <Button variant="ghost" size="icon">
                      <Info className="h-4 w-4 text-muted-foreground" />
                    </Button>
                  </TooltipTrigger>
                  <TooltipContent>
                    <p className="max-w-xs">{observacions}</p>
                  </TooltipContent>
                </Tooltip>
              )}
            </TableCell>

            {/* Columna 2: Edit */}
            <TableCell className="w-[30px] text-center">
              {canEdit && (
                <Button
                  variant="ghost"
                  size="icon"
                  onClick={() =>
                    handleEditClick(item.data, item.originalSheetRowIndex)
                  }
                >
                  <Edit className="h-4 w-4" />
                </Button>
              )}
            </TableCell>

            {/* Columna 3: Sign */}
            <TableCell className="w-[100px] text-right">
              {canUserSign && (
                <Button
                  variant="ghost"
                  className="w-[95px]"
                  onClick={() =>
                    handleSignClick(item.data, item.originalSheetRowIndex, "user")
                  }
                >
                  Signar Usuari
                </Button>
              )}
              {canDirectorSign && (
                <Button
                  variant="ghost"
                  className="w-[95px]"
                  onClick={() =>
                    handleSignClick(item.data, item.originalSheetRowIndex, "director")
                  }
                >
                  Signar Direcció
                </Button>
              )}
            </TableCell>
          </TableRow>
        )
      })
    )}
  </TableBody>
</Table>


            </div>
        {/*
          </TabsContent>

          <TabsContent value="modified">
            <div className="rounded-md border overflow-hidden shadow-sm mt-6">
              <Table>
                <TableHeader className="bg-primary text-primary-foreground">
                  <TableRow className="hover:bg-primary/90">
                    <TableHead className="w-[120px] text-primary-foreground">Estat</TableHead>
                    <TableHead className="text-primary-foreground">Tipus</TableHead>
                    <TableHead className="text-primary-foreground">Usuari</TableHead>
                    <TableHead className="text-primary-foreground">Inici</TableHead>
                    <TableHead className="text-primary-foreground">Fi</TableHead>
                    <TableHead className="text-primary-foreground">Duració</TableHead>
                    <TableHead className="w-[100px] text-primary-foreground">Signat Usuari</TableHead>
                    <TableHead className="w-[100px] text-primary-foreground">Signat Direcció</TableHead>
                    <TableHead className="text-primary-foreground">Esborrat</TableHead>
                    <TableHead className="w-[150px] text-right text-primary-foreground">Accions</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {modifiedIncidents.length === 1 ? (
                    <TableRow>
                      <TableCell colSpan={9} className="h-24 text-center text-muted-foreground">
                        No hi ha incidències modificades per mostrar.
                      </TableCell>
                    </TableRow>
                  ) : (
                    modifiedIncidents.slice(1).map(item => {
                      const isUserSigned = item.data[signaturaUsuariIndex] === 'TRUE';
                      const isDirectorSigned = item.data[signaturaDireccioIndex] === 'TRUE';
                      const observacions = item.data[observacionsIndex];

                      return (
                        <TableRow key={item.originalSheetRowIndex} className="hover:bg-muted/50 data-[state=selected]:bg-muted">
                          <TableCell>{item.data[tipusIndex]}</TableCell>
                          <TableCell>{item.data[userEmailIndex]}</TableCell>
                          <TableCell>{formatDateForDisplay(item.data[dataIniciIndex])} {item.data[horaIniciIndex]}</TableCell>
                          <TableCell>{formatDateForDisplay(item.data[dataFiIndex])} {item.data[horaFiIndex]}</TableCell>
                          <TableCell>{item.data[duracioIndex]}</TableCell>
                          <TableCell className="w-[100px]">{isUserSigned ? <CheckCircle className="h-4 w-4 text-green-600" /> : <XCircle className="h-4 w-4 text-red-600" />}</TableCell>
                          <TableCell className="w-[100px]">{isDirectorSigned ? <CheckCircle className="h-4 w-4 text-green-600" /> : <XCircle className="h-4 w-4 text-red-600" />}</TableCell>
                          <TableCell>{item.data[esborratIndex] === 'TRUE' ? 'Sí' : 'No'}</TableCell>
                          <TableCell className="w-[150px] text-right">
                            {observacions && (
                              <Tooltip>
                                <TooltipTrigger asChild>
                                  <Button variant="ghost" size="icon">
                                    <Info className="h-4 w-4 text-muted-foreground" />
                                  </Button>
                                </TooltipTrigger>
                                <TooltipContent><p className="max-w-xs">{observacions}</p></TooltipContent>
                              </Tooltip>
                            )}
                          </TableCell>
                        </TableRow>
                      );
                    })
                  )}
                </TableBody>
              </Table>
            </div>
          </TabsContent>

          <TabsContent value="summary">
            <AnnualSummaryView incidents={incidents} profile={profile} />
          </TabsContent>

          <TabsContent value="documentation">
            <DocumentationView accessToken={accessToken} />
          </TabsContent>
        </Tabs>
        */}
      </div>
    );
  }

  const renderScreen = () => {
    switch (currentScreen) {
      case 'login':
        return <LoginView onLogin={login} error={error} />;
      case 'home':
        return <HomeView onIncidentsClick={() => setCurrentScreen('incidents')} onCalendarClick={() => setCurrentScreen('calendar')} onGroupsClick={() => setCurrentScreen('groups')} onTICIncidentsClick={() => setCurrentScreen('tic-incidents')} onMantenimentClick={() => setCurrentScreen('manteniment-incidents')} onAvisosClick={() => setCurrentScreen('avisos')} accessToken={accessToken} profile={profile} onLogout={handleLogout} />;
      case 'incidents':
        return <IncidentsView onBackClick={() => setCurrentScreen('home')} />;
      case 'calendar':
        return <CalendarMainView onBackClick={() => setCurrentScreen('home')} accessToken={accessToken} profile={profile} />;
      case 'groups':
        return <MyGroupsView onBackClick={() => setCurrentScreen('home')} accessToken={accessToken} profile={profile} />;
      case 'avisos':
        return <AvisosView onBackClick={() => setCurrentScreen('home')} profile={profile} accessToken={accessToken} />;
      case 'tic-incidents':
        return <TICIncidentsView onBackClick={() => setCurrentScreen('home')} profile={profile} accessToken={accessToken} users={users} />;
      case 'manteniment-incidents':
        return <MantenimentView onBackClick={() => setCurrentScreen('home')} profile={profile} accessToken={accessToken} users={users} />;

      default:
        return <LoginView onLogin={login} error={error} />;
    }
  };

  return (
    <TooltipProvider>
      <div className="App">
        {/* Edit/Add Modal - Moved here to be accessible by IncidentsView */}
        {/* Edit/Add Modal - Moved here to be accessible by IncidentsView */}
        {/*
        
        */}

        {/* Signature Confirmation Modal */}
        <Dialog open={isSignaturePopupOpen} onOpenChange={(isOpen) => !isOpen && handleCancelSignature()}>
            <DialogContent>
                <DialogHeader>
                    <DialogTitle>Confirmar Signatura</DialogTitle>
                    <DialogDescription>Confirmació per signar la incidència.</DialogDescription>
                </DialogHeader>
                <p>{`Esteu segur que voleu signar aquesta incidència com a ${signatureType === 'user' ? 'Usuari' : 'Direcció'}?`}</p>
                <DialogFooter>
                    <Button variant="secondary" onClick={handleCancelSignature}>Cancel·lar</Button>
                    <Button onClick={handleConfirmSignature}>Confirmar</Button>
                </DialogFooter>
            </DialogContent>
        </Dialog>

        {renderScreen()}
      </div>
    </TooltipProvider>
  );
}

function AppWrapper() {
  return (
    <GoogleOAuthProvider clientId={process.env.REACT_APP_GOOGLE_CLIENT_ID}>
      <App />
    </GoogleOAuthProvider>
  );
}

export default AppWrapper;