import React from 'react';
import { Button } from '../ui/button';
import { 
  Dialog, 
  DialogContent, 
  DialogHeader, 
  DialogTitle, 
  DialogDescription, 
  DialogFooter, 
  DialogClose 
} from '../ui/dialog';
import { 
  AlertDialog, 
  AlertDialogAction, 
  AlertDialogCancel, 
  AlertDialogContent, 
  AlertDialogDescription, 
  AlertDialogFooter, 
  AlertDialogHeader, 
  AlertDialogTitle, 
  AlertDialogTrigger 
} from '../ui/alert-dialog';

const EventDetailModal = ({ event, onClose, calendarName, profile, onDelete, onEdit }) => {
  if (!event) return null;

  const { title, start, end, allDay, resource } = event;
  const description = resource?.description;
  const attendees = resource?.attendees || [];
  const canEdit = profile && (profile.role === 'Gestor' || profile.role === 'Direcció');

  const handleDelete = () => {
    onDelete(resource.id);
  };

  const formatDateTime = (date, isAllDayEvent) => {
    if (!date) return '';
    const options = { dateStyle: 'full' };
    if (!isAllDayEvent) {
      options.timeStyle = 'short';
    }
    return new Date(date).toLocaleString('ca-ES', options);
  }

  const displayEnd = allDay ? new Date(new Date(end).getTime() - 1) : end;

  return (
    <Dialog open={!!event} onOpenChange={(isOpen) => !isOpen && onClose()}>
      <DialogContent className="sm:max-w-lg">
        <DialogHeader>
          <DialogTitle>{title}</DialogTitle>
          <DialogDescription>Calendari: {calendarName}</DialogDescription>
        </DialogHeader>
        <div className="py-4 space-y-4">
          <div>
            <h4 className="font-semibold">Inici</h4>
            <p className="text-muted-foreground">{formatDateTime(start, allDay)}</p>
          </div>
          <div>
            <h4 className="font-semibold">Fi</h4>
            <p className="text-muted-foreground">{formatDateTime(displayEnd, allDay)}</p>
          </div>
          {description && (
            <div>
              <h4 className="font-semibold">Descripció</h4>
              <p className="text-muted-foreground">{description}</p>
            </div>
          )}
          {attendees.length > 0 && (
            <div>
              <h4 className="font-semibold">Convidats</h4>
              <ul className="list-disc list-inside text-muted-foreground">
                {attendees.map(att => <li key={att.email}>{att.email}</li>)}
              </ul>
            </div>
          )}
        </div>
        <DialogFooter className="justify-between">
          <div>
            {canEdit && (
              <div className="flex gap-2">
                <Button variant="outline" onClick={onEdit}>Editar</Button>
                <AlertDialog>
                  <AlertDialogTrigger asChild>
                    <Button variant="destructive">Esborrar</Button>
                  </AlertDialogTrigger>
                  <AlertDialogContent>
                    <AlertDialogHeader>
                      <AlertDialogTitle>Esteu segur?</AlertDialogTitle>
                      <AlertDialogDescription>
                        Aquesta acció no es pot desfer. S'esborrarà permanentment l'esdeveniment "{title}".
                      </AlertDialogDescription>
                    </AlertDialogHeader>
                    <AlertDialogFooter>
                      <AlertDialogCancel>Cancel·lar</AlertDialogCancel>
                      <AlertDialogAction onClick={handleDelete}>Continuar</AlertDialogAction>
                    </AlertDialogFooter>
                  </AlertDialogContent>
                </AlertDialog>
              </div>
            )}
          </div>
          <DialogClose asChild>
            <Button type="button" variant="secondary">Tancar</Button>
          </DialogClose>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
};

export default EventDetailModal;