import React, { useState, useEffect } from 'react';
import { Button } from './ui/button';
import { Input } from './ui/input';
import { Label } from './ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from './ui/select';
import { Textarea } from './ui/textarea';
import { DialogFooter } from './ui/dialog';

const AddMantenimentIncidentForm = ({
  incidentToEdit,
  originalSheetRowIndex,
  onSaveIncident,
  onClose,
  setError,
  profile,
  users,
}) => {
  const [incidentData, setIncidentData] = useState({
    'ID': '',
    'Tipus': '',
    'Qui fa la incidencia?': '',
    'Espai': '',
    'Objecte avariat': '',
    'Descripció': '',
    'Estat': 'Comunicat', // Default status
    'Data de comunicació': '',
    'Data de la darrera edició': '',
  });

  useEffect(() => {
    if (incidentToEdit) {
      // Map incidentToEdit array to incidentData state based on MantenimentGAS.js headers
      const mappedData = {
        'ID': incidentToEdit[0] || '',
        'Tipus': incidentToEdit[1] || '',
        'Qui fa la incidencia?': incidentToEdit[2] || '',
        'Espai': incidentToEdit[3] || '',
        'Objecte avariat': incidentToEdit[4] || '',
        'Descripció': incidentToEdit[5] || '',
        'Estat': incidentToEdit[6] || 'Comunicat',
        'Data de comunicació': incidentToEdit[7] || '',
        'Data de la darrera edició': incidentToEdit[8] || '',
      };
      setIncidentData(mappedData);
    } else {
      // For new incidents, set default values
      setIncidentData({
        'ID': '',
        'Tipus': '',
        'Qui fa la incidencia?': profile.email, // Default to current user's email
        'Espai': '',
        'Objecte avariat': '',
        'Descripció': '',
        'Estat': 'Comunicat',
        'Data de comunicació': '',
        'Data de la darrera edició': '',
      });
    }
  }, [incidentToEdit, profile]);

  const handleSelectChange = (name, value) => {
    setIncidentData(prevData => ({
      ...prevData,
      [name]: value
    }));
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setIncidentData(prevData => ({
      ...prevData,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    // Basic validation
    if (!incidentData['Tipus'].trim() || !incidentData['Qui fa la incidencia?'].trim() || !incidentData['Espai'].trim() || !incidentData['Objecte avariat'].trim() || !incidentData['Descripció'].trim()) {
      setError("Tots els camps són obligatoris.");
      return;
    }

    // Data to send to GAS (order matters)
    const dataToSend = {
      'ID': incidentData['ID'],
      'Tipus': incidentData['Tipus'],
      'Qui fa la incidencia?': incidentData['Qui fa la incidencia?'],
      'Espai': incidentData['Espai'],
      'Objecte avariat': incidentData['Objecte avariat'],
      'Descripció': incidentData['Descripció'],
      'Estat': incidentData['Estat'],
      'Data de comunicació': incidentData['Data de comunicació'], // GAS will set this for new incidents
      'Data de la darrera edició': incidentData['Data de la darrera edició'], // GAS will set this for new/updated incidents
      'rowIndex': originalSheetRowIndex // Pass rowIndex for updates
    };

    try {
      await onSaveIncident(dataToSend, originalSheetRowIndex);
      onClose();
    } catch (err) {
      console.error("Error saving incident:", err);
      setError("Error en guardar la incidència. (Detalls: " + err.message + ")");
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div className="space-y-2">
          <Label htmlFor="quiFaLaIncidencia">Qui fa la incidencia?</Label>
          <Select
            name="Qui fa la incidencia?"
            value={incidentData['Qui fa la incidencia?']}
            onValueChange={(value) => handleSelectChange('Qui fa la incidencia?', value)}
            required
            disabled={profile?.role === 'Usuari' && !incidentToEdit} // Disable for new incidents if user role
          >
            <SelectTrigger id="quiFaLaIncidencia">
              <SelectValue placeholder="Seleccioneu un usuari" />
            </SelectTrigger>
            <SelectContent>
              {users.sort((a, b) => a.name.localeCompare(b.name)).map((user, index) => (
                <SelectItem key={index} value={user.email}>{user.name} ({user.email})</SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
        <div className="space-y-2">
          <Label htmlFor="tipus">Tipus</Label>
          <Select
            name="Tipus"
            value={incidentData['Tipus']}
            onValueChange={(value) => handleSelectChange('Tipus', value)}
            required
          >
            <SelectTrigger id="tipus">
              <SelectValue placeholder="Seleccioneu un tipus" />
            </SelectTrigger>
            <SelectContent>
                <SelectItem value="Mobiliari">Mobiliari</SelectItem>
                <SelectItem value="Lavabos i aigua">Lavabos i aigua</SelectItem>
                <SelectItem value="Finestres">Finestres</SelectItem>
                <SelectItem value="Terra i parets">Terra i parets</SelectItem>
                <SelectItem value="Portes i tancaments">Portes i tancaments</SelectItem>
                <SelectItem value="Altres">Altres</SelectItem>
            </SelectContent>
          </Select>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div className="space-y-2">
          <Label htmlFor="espai">Espai</Label>
          <Select
            name="Espai"
            value={incidentData['Espai']}
            onValueChange={(value) => handleSelectChange('Espai', value)}
            required
          >
            <SelectTrigger id="espai">
                <SelectValue placeholder="Seleccioneu un espai" />
            </SelectTrigger>
            <SelectContent>
                <SelectItem value="Aula 4.1 informàtica">Aula 4.1 informàtica</SelectItem>
                <SelectItem value="Aula 4.2">Aula 4.2</SelectItem>
                <SelectItem value="Aula 4.3">Aula 4.3</SelectItem>
                <SelectItem value="Aula 4.4">Aula 4.4</SelectItem>
                <SelectItem value="Aula 4.5 informàtica 2">Aula 4.5 informàtica 2</SelectItem>
                <SelectItem value="Distribució planta 4">Distribució planta 4</SelectItem>
                <SelectItem value="Sala de professors">Sala de professors</SelectItem>
                <SelectItem value="Direcció, administració i recepció">Direcció, administració i recepció</SelectItem>
                <SelectItem value="Aula 5.1">Aula 5.1</SelectItem>
                <SelectItem value="Aula 5.2">Aula 5.2</SelectItem>
                <SelectItem value="Aula 5.3">Aula 5.3</SelectItem>
                <SelectItem value="Aula 5.4">Aula 5.4</SelectItem>
                <SelectItem value="Distribució planta 5">Distribució planta 5</SelectItem>
                <SelectItem value="Altres">Altres</SelectItem>
            </SelectContent>
          </Select>
        </div>
        <div className="space-y-2">
          <Label htmlFor="objecteAvariat">Objecte avariat</Label>
          <Input
            type="text"
            id="objecteAvariat"
            name="Objecte avariat"
            value={incidentData['Objecte avariat']}
            onChange={handleChange}
            required
          />
        </div>
      </div>

      <div className="space-y-2">
        <Label htmlFor="descripcio">Descripció</Label>
        <Textarea
          id="descripcio"
          name="Descripció"
          rows="3"
          value={incidentData['Descripció']}
          onChange={handleChange}
          required
        />
      </div>

      {incidentToEdit && ( // Only show status and dates for existing incidents
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div className="space-y-2">
            <Label htmlFor="estat">Estat</Label>
            <Select
              name="Estat"
              value={incidentData['Estat']}
              onValueChange={(value) => handleSelectChange('Estat', value)}
              required
            >
                <SelectTrigger id="estat">
                    <SelectValue placeholder="Selecciona un estat" />
                </SelectTrigger>
                <SelectContent>
                    <SelectItem value="Comunicat">Comunicat</SelectItem>
                    <SelectItem value="En reparació">En reparació</SelectItem>
                    <SelectItem value="Solucionat">Solucionat</SelectItem>
                    <SelectItem value="Avariat">Avariat</SelectItem>
                </SelectContent>
            </Select>
          </div>
          <div className="space-y-2">
            <Label htmlFor="dataComunicacio">Data de comunicació</Label>
            <Input
              type="text"
              id="dataComunicacio"
              name="Data de comunicació"
              value={incidentData['Data de comunicació']}
              readOnly
            />
          </div>
        </div>
      )}

      {incidentToEdit && (
        <div className="space-y-2">
          <Label htmlFor="dataDarreraEdicio">Data de la darrera edició</Label>
          <Input
            type="text"
            id="dataDarreraEdicio"
            name="Data de la darrera edició"
            value={incidentData['Data de la darrera edició']}
            readOnly
          />
        </div>
      )}

      <DialogFooter>
        <Button type="button" variant="secondary" onClick={onClose}>
            Cancel·lar
        </Button>
        <Button type="submit">
          {incidentToEdit ? 'Guardar Canvis' : 'Afegir Incidència'}
        </Button>
      </DialogFooter>
    </form>
  );
};

export default AddMantenimentIncidentForm;
