import React from 'react';
import { Button } from '../ui/button';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '../ui/select';
import { ChevronLeft, ChevronRight, Plus } from 'lucide-react';

const CalendarToolbar = ({ 
  calendars, 
  activeCalendar, 
  setActiveCalendar, 
  currentView, 
  setCurrentView, 
  profile, 
  onAddEventClick,
  date, // from RBC
  onNavigate // from RBC
}) => {
  
  const canEdit = profile && (profile.role === 'Gestor' || profile.role === 'Direcció');
  const isIncidentsCalendar = activeCalendar === 'incidents';

  const navigate = (action) => {
    onNavigate(action);
  };

  const handleViewChange = (view) => {
    setCurrentView(view);
  };

  const label = () => {
    const month = date.toLocaleString('ca-ES', { month: 'long' });
    const year = date.getFullYear();
    return (
      <span className="capitalize">
        {month} {year}
      </span>
    );
  };

  return (
    <div className="flex flex-wrap items-center justify-between gap-4 rounded-lg bg-gray-50 p-4">
      <div className="flex items-center gap-2">
        <Select value={activeCalendar} onValueChange={setActiveCalendar}>
          <SelectTrigger className="w-[180px]">
            <SelectValue placeholder="Selecciona un calendari" />
          </SelectTrigger>
          <SelectContent>
            {Object.values(calendars).map(calendar => (
              <SelectItem key={calendar.id} value={calendar.id}>{calendar.name}</SelectItem>
            ))}
          </SelectContent>
        </Select>
        {canEdit && !isIncidentsCalendar && (
          <Button onClick={onAddEventClick} size="sm">
            <Plus className="mr-2 h-4 w-4" /> Afegir Esdeveniment
          </Button>
        )}
      </div>

      <div className="flex items-center gap-4">
        <Button variant="outline" size="sm" onClick={() => navigate('TODAY')}>Avui</Button>
        <div className="flex items-center gap-1">
          <Button variant="outline" size="icon" className="h-8 w-8" onClick={() => navigate('PREV')}><ChevronLeft className="h-4 w-4" /></Button>
          <h3 className="text-lg font-semibold w-32 text-center">{label()}</h3>
          <Button variant="outline" size="icon" className="h-8 w-8" onClick={() => navigate('NEXT')}><ChevronRight className="h-4 w-4" /></Button>
        </div>
      </div>

      <div className="flex items-center gap-2">
        <Select value={currentView} onValueChange={handleViewChange}>
            <SelectTrigger className="w-[120px]">
                <SelectValue placeholder="Vista" />
            </SelectTrigger>
            <SelectContent>
                <SelectItem value="month">Mes</SelectItem>
                <SelectItem value="week">Setmana</SelectItem>
                <SelectItem value="day">Dia</SelectItem>
                <SelectItem value="agenda">Agenda</SelectItem>
            </SelectContent>
        </Select>
      </div>
    </div>
  );
};

export default CalendarToolbar;