import React, { useState, useEffect } from 'react';
import { getSheetData, callGASFunction } from '../googleServices';
import { Button } from './ui/button';
import { Card, CardHeader, CardTitle, CardContent, CardFooter, CardDescription } from './ui/card';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter, DialogClose } from './ui/dialog';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from './ui/select';
import { Checkbox } from './ui/checkbox';
import { Tabs, TabsContent, TabsList, TabsTrigger } from './ui/tabs';
import { Alert, AlertDescription } from './ui/alert';
import { Table, TableHeader, TableBody, TableRow, TableHead, TableCell } from './ui/table';
import { ExternalLink, Users, Download, RefreshCw, UserCog } from 'lucide-react';

const AdminView = ({ config, classrooms, teachers, groups, chats, onUpdateConfig, loading, profile, accessToken, onSyncLists, onUpdateStudents, onUpdateTeachers, onSyncMembers }) => {
  const [editableConfig, setEditableConfig] = useState(config || []);
  const [students, setStudents] = useState([]);
  const [selectedClassroom, setSelectedClassroom] = useState(null);
  const [loadingStudents, setLoadingStudents] = useState(false);
  const [studentsError, setStudentsError] = useState(null);
  const [selectedTeacher, setSelectedTeacher] = useState('');

  useEffect(() => {
    setEditableConfig(config);
  }, [config]);

  useEffect(() => {
    const fetchStudents = async () => {
      setLoadingStudents(true);
      setStudentsError(null);
      try {
        const studentsData = await getSheetData('Alumnes', accessToken);
        setStudents(studentsData);
      } catch (err) {
        setStudentsError(err.message);
      } finally {
        setLoadingStudents(false);
      }
    };
    fetchStudents();
  }, [accessToken]);

  const handleConfigChange = (index, field, value) => {
    const newConfig = [...editableConfig];
    newConfig[index][field] = value;
    setEditableConfig(newConfig);
  };

  const handleSaveChanges = () => {
    onUpdateConfig(editableConfig);
  };

  const handleExportStudents = async (classroom) => {
    const courseName = classroom.name;
    const studentsList = students.filter(student => student[0] === courseName.replace('DEL - ', '')).sort((a, b) => a[1].localeCompare(b[1]));
    const groupName = classroom.groupName || 'N/A';
    const teachersForClassroom = teachers.filter(teacher => teacher.classroomName === courseName);
    const teacherNames = teachersForClassroom.map(teacher => teacher.name);

    try {
      const dataToExport = studentsList.map(student => [student[1], student[2]]);
      const response = await callGASFunction('createStudentsSheet', accessToken, {
        courseName: courseName,
        studentsData: JSON.stringify(dataToExport),
        groupName: groupName,
        teacherNames: JSON.stringify(teacherNames),
        newOwnerEmail: profile.email || ''
      });
      
      if (response.success && response.url) {
        window.open(response.url, '_blank');
        alert(`Full de càlcul creat i obert en una nova pestanya: ${response.url}`);
      } else {
        alert(`Error: ${response.message}`);
      }
    } catch (error) {
      alert(`Error al exportar: ${error.message}`);
    }
  };

  const classroomStudents = selectedClassroom
    ? students.filter(student => student[0] === selectedClassroom.name.replace('DEL - ', '')).sort((a, b) => a[1].localeCompare(b[1]))
    : [];

  const uniqueTeachers = Array.from(new Map(teachers.map(teacher => [teacher.email, teacher])).values()).sort((a, b) => a.name.localeCompare(b.name));

  const filteredClassrooms = selectedTeacher
    ? classrooms.filter(classroom => teachers.some(teacher => teacher.email === selectedTeacher && teacher.classroomName === classroom.name))
    : classrooms;

  const activeClassrooms = filteredClassrooms.filter(classroom => {
    const classroomConfig = config.find(c => c[5] === classroom.id) || [];
    return classroomConfig[0] === 'TRUE';
  });

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle>Accions de Sincronització</CardTitle>
          <CardDescription>Executa processos per actualitzar les dades des de les fonts de Google.</CardDescription>
        </CardHeader>
        <CardContent className="grid grid-cols-2 md:grid-cols-4 gap-4">
          <Button onClick={onSyncLists} disabled={loading}><RefreshCw className="mr-2 h-4 w-4"/>Sinc. Llistes</Button>
          <Button onClick={onUpdateStudents} disabled={loading}><UserCog className="mr-2 h-4 w-4"/>Actual. Alumnes</Button>
          <Button onClick={onUpdateTeachers} disabled={loading}><UserCog className="mr-2 h-4 w-4"/>Actual. Professors</Button>
          <Button onClick={onSyncMembers} disabled={loading}><RefreshCw className="mr-2 h-4 w-4"/>Sinc. Membres</Button>
        </CardContent>
      </Card>

      <Tabs defaultValue="groups">
        <TabsList className="grid w-full grid-cols-2">
          <TabsTrigger value="groups">Llistat de grups</TabsTrigger>
          {profile.role === 'Direcció' && <TabsTrigger value="config">Configuració</TabsTrigger>}
        </TabsList>

        <TabsContent value="groups">
          <Card className="mt-4">
            <CardHeader>
              <CardTitle>Llistat de Classrooms Actius</CardTitle>
              <CardDescription>
                <label htmlFor="teacher-select" className="text-sm font-medium">Filtra per professor:</label>
                <Select onValueChange={setSelectedTeacher} value={selectedTeacher}>
                  <SelectTrigger className="mt-2"><SelectValue placeholder="Tots els professors" /></SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">Tots els professors</SelectItem>
                    {uniqueTeachers.map((teacher, index) => (
                      <SelectItem key={index} value={teacher.email}>{teacher.name}</SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </CardDescription>
            </CardHeader>
            <CardContent>
              {studentsError && <Alert variant="destructive" className="mb-4"><AlertDescription>{studentsError}</AlertDescription></Alert>}
              {loadingStudents && <p className="text-center text-muted-foreground">Carregant alumnes...</p>}
              <div className="rounded-md border overflow-hidden shadow-sm">
                <Table>
                  <TableHeader className="bg-primary text-primary-foreground">
                    <TableRow className="hover:bg-primary/90">
                      <TableHead className="text-primary-foreground">Nom Classroom</TableHead>
                      <TableHead className="text-primary-foreground">Professors</TableHead>
                      <TableHead className="text-primary-foreground">Classroom</TableHead>
                      <TableHead className="text-primary-foreground">Chat</TableHead>
                      <TableHead className="text-primary-foreground">Grup</TableHead>
                      <TableHead className="text-right text-primary-foreground">Accions</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {activeClassrooms.length === 0 ? (
                      <TableRow>
                        <TableCell colSpan={6} className="h-24 text-center text-muted-foreground">
                          No s'han trobat aules.
                        </TableCell>
                      </TableRow>
                    ) : (
                      activeClassrooms.map((classroom) => {
                        const teachersForClass = teachers.filter(t => t.classroomName === classroom.name);
                        return (
                          <TableRow key={classroom.id} className="hover:bg-muted/50 data-[state=selected]:bg-muted">
                            <TableCell className="font-medium">{classroom.name}</TableCell>
                            <TableCell>{teachersForClass.map(t => t.name).join(', ')}</TableCell>
                            <TableCell>
                              <Button asChild variant="default" size="sm">
                                <a href={classroom.alternateLink} target="_blank" rel="noopener noreferrer">Classroom <ExternalLink className="ml-1 h-3 w-3"/></a>
                              </Button>
                            </TableCell>
                            <TableCell>
                              {classroom.chatId ? (
                                <Button asChild variant="secondary" size="sm">
                                  <a href={`https://chat.google.com/room/${classroom.chatId.split('/').pop()}`} target="_blank" rel="noopener noreferrer">Chat <ExternalLink className="ml-1 h-3 w-3"/></a>
                                </Button>
                              ) : <span className="text-muted-foreground">N/A</span>}
                            </TableCell>
                            <TableCell>
                              {classroom.groupUrl ? (
                                <Button asChild variant="outline" size="sm">
                                   <a href={classroom.groupUrl} target="_blank" rel="noopener noreferrer">Grup <ExternalLink className="ml-1 h-3 w-3"/></a>
                                </Button>
                              ) : <span className="text-muted-foreground">N/A</span>}
                            </TableCell>
                            <TableCell className="text-right">
                              <Button variant="ghost" size="icon" onClick={() => setSelectedClassroom(classroom)}><Users className="h-4 w-4"/></Button>
                              <Button variant="ghost" size="icon" onClick={() => handleExportStudents(classroom)}><Download className="h-4 w-4"/></Button>
                            </TableCell>
                          </TableRow>
                        )
                      })
                    )}</TableBody>
                </Table>
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        {profile.role === 'Direcció' && (
          <TabsContent value="config">
            <Card className="mt-4">
              <CardHeader className="flex flex-row items-center justify-between">
                <div>
                  <CardTitle>Configuració de Classrooms</CardTitle>
                  <CardDescription>Activa o desactiva classrooms i associa els seus grups i xats.</CardDescription>
                </div>
                <Button onClick={handleSaveChanges} disabled={loading}>{loading ? 'Guardant...' : 'Guardar Canvis'}</Button>
              </CardHeader>
              <CardContent>
                <div className="border rounded-lg overflow-hidden">
                  <div className="grid grid-cols-12 gap-4 font-semibold bg-gray-50 p-3 border-b">
                    <div className="col-span-1">Actiu</div>
                    <div className="col-span-4">Nom Classroom</div>
                    <div className="col-span-3">Google Group associat</div>
                    <div className="col-span-4">Google Chat associat</div>
                  </div>
                  <div className="max-h-[60vh] overflow-y-auto">
                    {editableConfig.map((row, index) => (
                      <div key={index} className="grid grid-cols-12 gap-4 items-center p-3 border-b last:border-b-0">
                        <div className="col-span-1"><Checkbox checked={row[0] === 'TRUE'} onCheckedChange={(checked) => handleConfigChange(index, 0, checked ? 'TRUE' : 'FALSE')} /></div>
                        <div className="col-span-4 text-sm">{row[1]}</div>
                        <div className="col-span-3">
                          <Select value={row[2]} onValueChange={(value) => handleConfigChange(index, 2, value)}>
                            <SelectTrigger><SelectValue placeholder="-" /></SelectTrigger>
                            <SelectContent>
                              <SelectItem value="none">-</SelectItem>
                              {groups.map((group, i) => <SelectItem key={i} value={group}>{group}</SelectItem>)}
                            </SelectContent>
                          </Select>
                        </div>
                        <div className="col-span-4">
                          <Select value={row[3]} onValueChange={(value) => handleConfigChange(index, 3, value)}>
                            <SelectTrigger><SelectValue placeholder="-" /></SelectTrigger>
                            <SelectContent>
                              <SelectItem value="none">-</SelectItem>
                              {chats.map((chat, i) => <SelectItem key={i} value={chat.displayName}>{chat.displayName}</SelectItem>)}
                            </SelectContent>
                          </Select>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        )}
      </Tabs>

      {selectedClassroom && (
        <Dialog open={!!selectedClassroom} onOpenChange={(isOpen) => !isOpen && setSelectedClassroom(null)}>
          <DialogContent className="sm:max-w-2xl">
            <DialogHeader>
              <DialogTitle>Alumnes de {selectedClassroom.name}</DialogTitle>
            </DialogHeader>
            <div className="max-h-[60vh] overflow-y-auto p-4">
              {classroomStudents.length > 0 ? (
                <div className="rounded-md border overflow-hidden">
                  <Table>
                    <TableHeader className="bg-muted/50">
                      <TableRow>
                        <TableHead>Nom Complet</TableHead>
                        <TableHead>Email</TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {classroomStudents.map((student, idx) => (
                        <TableRow key={idx}>
                          <TableCell>{student[1]}</TableCell>
                          <TableCell>{student[2]}</TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </div>
              ) : (
                <p className="text-center text-muted-foreground">No hi ha alumnes per a aquesta classe.</p>
              )}
            </div>
            <DialogFooter>
              <DialogClose asChild><Button type="button" variant="secondary">Tancar</Button></DialogClose>
            </DialogFooter>
          </DialogContent>
        </Dialog>
      )}
    </div>
  );
};

export default AdminView;