import React, { useState, useEffect } from 'react';
import { format, startOfWeek, addDays, startOfMonth, endOfMonth, endOfWeek, addMonths, subMonths, isSameMonth, isSameDay, parseISO } from 'date-fns';
import { DayPicker } from 'react-day-picker';
import 'react-day-picker/dist/style.css'; // Default styles for react-day-picker

// Import Shadcn UI components
import { Button } from '../ui/button';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '../ui/select';
import { Card, CardContent, CardHeader, CardTitle } from '../ui/card';

const CALENDAR_VIEWS = {
  MONTH: 'month',
  WEEK: 'week',
  DAY: 'day',
  AGENDA: 'agenda',
};

const CATEGORY_COLORS = {
  'Coordinador': '#F59E0B', // Orange
  'Entrevista': '#8B5CF6', // Purple
  'Activitats': '#10B981', // Green
  'Reunions': '#3B82F6', // Blue
  'JAV': '#84CC16', // Lime Green
  'Calendari': '#FACC15', // Yellow
  'CSI': '#EF4444', // Red
  'EIB': '#22D3EE', // Cyan
  'FB': '#6366F1', // Indigo
  'Proves': '#EC4899', // Pink
};

const NewCalendarView = ({ events, currentCalendarId, profile }) => {
  const [currentView, setCurrentView] = useState(CALENDAR_VIEWS.MONTH);
  const [month, setMonth] = useState(new Date());
  const [selectedDate, setSelectedDate] = useState(new Date());

  // Helper to get event color
  const getEventColor = (event) => {
    const category = (event.title || '').split(':')[0].trim();
    return CATEGORY_COLORS[category] || '#288185'; // Default to primary color
  };

  // Render Month View
  const renderMonthView = () => {
    const days = [];
    const startDate = startOfWeek(startOfMonth(month));
    const endDate = endOfWeek(endOfMonth(month));
    let day = startDate;

    while (day <= endDate) {
      for (let i = 0; i < 7; i++) {
        const formattedDate = format(day, 'd');
        const isCurrentMonth = isSameMonth(day, month);
        const isToday = isSameDay(day, new Date());
        const dayEvents = events.filter(event => {
          let eventStartDate;
          if (typeof event.start === 'string') {
            eventStartDate = parseISO(event.start);
          } else if (event.start instanceof Date) {
            eventStartDate = event.start;
          } else {
            // If event.start is neither a string nor a Date object, it's invalid for comparison
            return false;
          }
          return isSameDay(eventStartDate, day);
        });

        days.push(
          <div
            key={day.toISOString()}
            className={`border p-2 h-32 relative ${!isCurrentMonth ? 'bg-gray-100 text-gray-400' : ''} ${isToday ? 'bg-blue-100' : ''}`}
          >
            <span className="text-sm font-semibold">{formattedDate}</span>
            <div className="events-container mt-1 space-y-1">
              {dayEvents.map(event => (
                <div
                  key={event.title + event.start}
                  className="text-xs text-white p-1 rounded-sm truncate"
                  style={{ backgroundColor: getEventColor(event) }}
                >
                  {event.title}
                </div>
              ))}
            </div>
          </div>
        );
        day = addDays(day, 1);
      }
    }

    return (
      <div className="grid grid-cols-7 gap-px border-t border-l">
        {['Dilluns', 'Dimarts', 'Dimecres', 'Dijous', 'Divendres', 'Dissabte', 'Diumenge'].map(dayName => (
          <div key={dayName} className="border-b border-r p-2 text-center font-semibold text-sm bg-gray-50">
            {dayName}
          </div>
        ))}
        {days}
      </div>
    );
  };

  // Render Week View (Placeholder)s
  const renderWeekView = () => {
    return <div className="p-4">Week View - Coming Soon!</div>;
  };

  // Render Day View (Placeholder)
  const renderDayView = () => {
    return <div className="p-4">Day View - Coming Soon!</div>;
  };

  // Render Agenda View (Placeholder)
  const renderAgendaView = () => {
    return <div className="p-4">Agenda View - Coming Soon!</div>;
  };

  return (
    <div className="space-y-4">
      <div className="flex justify-between items-center">
        <div className="flex space-x-2">
          <Button onClick={() => setMonth(subMonths(month, 1))}>Previous</Button>
          <Button onClick={() => setMonth(addMonths(month, 1))}>Next</Button>
        </div>
        <h2 className="text-xl font-bold">{format(month, 'MMMM yyyy')}</h2>
        <div className="flex space-x-2">
          {Object.values(CALENDAR_VIEWS).map(view => (
            <Button
              key={view}
              variant={currentView === view ? 'default' : 'outline'}
              onClick={() => setCurrentView(view)}
            >
              {view.charAt(0).toUpperCase() + view.slice(1)}
            </Button>
          ))}
        </div>
      </div>

      <Card>
        <CardContent>
          {currentView === CALENDAR_VIEWS.MONTH && renderMonthView()}
          {currentView === CALENDAR_VIEWS.WEEK && renderWeekView()}
          {currentView === CALENDAR_VIEWS.DAY && renderDayView()}
          {currentView === CALENDAR_VIEWS.AGENDA && renderAgendaView()}
        </CardContent>
      </Card>
    </div>
  );
};

export default NewCalendarView;
