import React, { useState, useEffect } from 'react';
import { getAllAvisos, toggleAvisoStatus } from '../avisosService';
import AvisoForm from './AvisoForm';
import { Button } from './ui/button';
import { Card, CardHeader, CardTitle, CardContent, CardFooter } from './ui/card';
import { Badge } from './ui/badge';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from './ui/dialog';
import { Alert, AlertDescription } from './ui/alert';
import { ArrowLeft, Plus } from 'lucide-react';

const AvisosView = ({ onBackClick, profile, accessToken }) => {
  const [avisos, setAvisos] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [isFormOpen, setIsFormOpen] = useState(false);

  const canManage = profile.role === 'Direcció';

  const fetchAllAvisos = async () => {
    try {
      setLoading(true);
      const allAvisos = await getAllAvisos(accessToken);
      setAvisos(allAvisos);
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchAllAvisos();
  }, [accessToken]);

  const handleToggleStatus = async (id) => {
    try {
      await toggleAvisoStatus(id, accessToken);
      fetchAllAvisos(); 
    } catch (err) {
      alert(`Error al canviar l'estat: ${err.message}`);
    }
  };

  const handleAvisoAdded = () => {
    setIsFormOpen(false);
    fetchAllAvisos();
  }

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <header className="flex justify-between items-center mb-6 pb-4 border-b">
        <div className="flex items-center gap-4">
          <Button onClick={onBackClick} variant="outline" size="icon">
            <ArrowLeft className="h-4 w-4" />
          </Button>
          <h1 className="text-2xl font-bold">Historial d'Avisos</h1>
        </div>
        {canManage && (
          <Button onClick={() => setIsFormOpen(true)}><Plus className="mr-2 h-4 w-4"/>Afegir Nou Avís</Button>
        )}
      </header>

      {error && <Alert variant="destructive" className="mb-4"><AlertDescription>{error}</AlertDescription></Alert>}

      {loading ? (
        <p className="text-center text-muted-foreground">Carregant avisos...</p>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {avisos.map(aviso => (
            <Card key={aviso.ID} className="flex flex-col">
              <CardHeader>
                <CardTitle>{aviso.Titol}</CardTitle>
              </CardHeader>
              <CardContent className="flex-grow" dangerouslySetInnerHTML={{ __html: aviso.Contingut }} />
              <CardFooter className="flex justify-between items-center bg-gray-50 p-4 mt-auto">
                <div>
                  <span className="text-sm text-muted-foreground">Estat: </span>
                  <Badge variant={aviso.Actiu ? 'default' : 'destructive'}>
                    {aviso.Actiu ? 'Actiu' : 'Inactiu'}
                  </Badge>
                </div>
                {canManage && (
                  <Button 
                    variant={aviso.Actiu ? 'outline' : 'default'}
                    size="sm"
                    onClick={() => handleToggleStatus(aviso.ID)}
                  >
                    {aviso.Actiu ? 'Desactivar' : 'Activar'}
                  </Button>
                )}
              </CardFooter>
            </Card>
          ))}
        </div>
      )}

      <Dialog open={isFormOpen} onOpenChange={setIsFormOpen}>
        <DialogContent className="sm:max-w-4xl">
          <DialogHeader>
            <DialogTitle>Afegir Nou Avís</DialogTitle>
          </DialogHeader>
          <AvisoForm 
            accessToken={accessToken} 
            onCancel={() => setIsFormOpen(false)}
            onAvisoAdded={handleAvisoAdded}
          />
        </DialogContent>
      </Dialog>
    </div>
  );
};

export default AvisosView;
